*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content{
  height: 100vh;
}

.navbar{
  background-color: azure !important;
}

.register-page{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}


.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon{
  color: black;
}

.inactive-icon{
  color: lightgray;
}


.switch-icons{
  border: 1px solid rgb(70, 63, 63);
  border-radius: 5px;
  padding: 10px 15px;

}